let baseUrl = 'http://43.139.147.42:8080/Investment-1.2-SNAPSHOT'; //
export const request = (options = {}) => {
	//Promise
	return new Promise((resolve, reject) => {
		// 
		uni.request({
			url: baseUrl + options.url || '', //API
			method: options.method || 'GET', //,GET
			data: options.data || {}, //data,
		}).then(data => {
			let [err, res] = data;
			resolve(res);
		}).catch(error => {
			reject(error);
		})
	})
}

